<template>
  <div>
      <van-image v-for="(item,index) in subData.answerCard" 
          :key="index" width=100% 
          :src="item"
          @click="imagePreview(subData.answerCard,index)">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
        </van-image>
      <div class="table">
        <div style="height:10px;"/>
        <div class="title">客观题</div>       
        <div class="questions">
            <div>题号</div>
            <div>我的答案</div>
            <div>标准答案</div>
            <div>得分</div>
        </div>
        <div class="questions" v-for="(item,index) in subData.question.THs" :key="index" v-if="index<maxSubExpand">
            <p :style="viewWrongQuestion(item)">{{item.TH}}</p>
            <p :style="viewWrongQuestion(item)">{{item.Content}}</p>
            <p :style="viewWrongQuestion(item)">{{item.trueAnswer}}</p>
            <p :style="viewWrongQuestion(item)">{{item.Score + "/" + item.totalScore}}</p>
        </div>
        <div v-if="subData.question.THs.length>5" class="expand" @click="onExpand(0)">
          <div v-if="maxSubExpand<subData.question.THs.length"><van-icon name="arrow-down" size="20"/>展开</div>
          <div v-else><van-icon name="arrow-up" size="20"/>收起</div>  
        </div>  
        <div v-else style="height:10px;"/>      
      </div>

      <div class="table">
        <div style="height:10px;"/>
        <div class="title">主观题</div>  
        <div class="questions">
            <div>题号</div>
            <div>总分</div>
            <div>得分</div>
            <div>平均分</div>
        </div>
        <div class="questions" v-for="(item,index) in subData.question.TZs" :key="index" v-if="index<maxObjExpand">
            <p>{{item.TH}}</p>
            <p>{{item.totalScore}}</p>            
            <p>{{item.Score}}</p>
            <p>{{item.avg}}</p>
        </div>   
        <div v-if="subData.question.TZs.length>5" class="expand" @click="onExpand(1)">
          <div v-if="maxObjExpand<subData.question.TZs.length"><van-icon name="arrow-down" size="20"/>展开</div>
        <div v-else><van-icon name="arrow-up" size="20"/>收起</div>  
        </div>  
        <div v-else style="height:10px;"/>   
      </div>

      <div v-if="subData.knowledge" class="table">
        <div style="height:10px;"/>
        <div class="title">知识点分析</div>  
        <div class="knowledges">
            <div>知识点名称</div>
            <div>得分</div>
            <div>平均分</div>
            <div>评价</div>
        </div>
        <div class="knowledges" v-for="(item,index) in subData.knowledge" :key="index" v-if="index<maxKlExpand">
            <p>{{item[0]}}</p>
            <p>{{item[1]}}</p>            
            <p>{{item[2]}}</p>
            <p>{{item[5]}}</p>
        </div>   
        <div v-if="subData.knowledge.length>5" class="expand" @click="onExpand(2)">
          <div v-if="maxKlExpand<subData.knowledge.length"><van-icon name="arrow-down" size="20"/>展开</div>
        <div v-else><van-icon name="arrow-up" size="20"/>收起</div>  
        </div>  
        <div v-else style="height:10px;"/>   
      </div>

      <div style="height: 3rem;"/>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from 'vant';
export default{
  props:['subData'],
  data () {
    return {
      maxSubExpand:5,
      maxObjExpand:5, 
      maxKlExpand:5,     
    };
  },
  mounted(){
    this.numSub = 0;
    this.numObj = 0;
  },
  methods:{
    viewWrongQuestion(item){
      return item.Score==item.totalScore?'':'color:red;';
    },
    imagePreview(images,index){
      ImagePreview({images:images,showIndex:true,loop:false,maxZoom:5,startPosition:index});
    },
    onExpand(flag){
      if(flag == 0){
        if(this.maxSubExpand>=this.subData.question.THs.length)
          this.maxSubExpand = 5;
        else
          this.maxSubExpand =this.subData.question.THs.length;
      }else if(flag==1){
        if(this.maxObjExpand>=this.subData.question.TZs.length)
          this.maxObjExpand = 5;
        else
          this.maxObjExpand =this.subData.question.TZs.length;        
      }else{
        if(this.maxKlExpand>=this.subData.knowledge.length)
          this.maxKlExpand = 5;
        else
          this.maxKlExpand =this.subData.knowledge.length;        
      }

    },
  },
};
</script>

<style lang="less" scoped>
.table{
  border-radius: 10px;
  background-color: rgb(253, 251, 220);//rgb(221, 221, 221);
  margin: 20px 10px;
  color:black;
  line-height: 14px;
  padding:  10px;
}
.questions{
  display: grid;
  justify-items:center;
  align-items:center;
  width: 100%;
  height: 40px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto auto;
}
.knowledges{
  display: grid;
  justify-items:center;
  align-items:center;
  width: 100%;
  height: 40px;
  grid-template-columns: 4fr 1fr 1fr 1fr;
  grid-template-rows: auto auto;
}
.title{
  text-align:center; 
  width: 100%;
  line-height: 20px;
  font-size: 16px;
  height: 40px;
}
.expand{
  height: 20px;
  text-align: center;
}
</style>



