<template>
  <div>
      <div class="table">
        <div class="stat" v-if="maxExpand">
            <div>科目</div>
            <div>得分</div>
            <div>总分</div>
            <div>联考排名</div>
            <div>学校排名</div> 
            <div>班级排名</div>                        
        </div>
        <div class="stat" v-for="(item,index) in subjects" :key="index" v-if="index<maxExpand && item.code>=0">
            <p>{{item.srcKM}}</p>
            <p>{{item.myScore}}</p>
            <p>{{item.fullScore}}</P>
            <p>{{item.us}}</p>
            <p>{{item.ss}}</p>
            <p>{{item.cs}}</p>
        </div>
        <div class="expand" @click="onExpand">
          <div v-if="maxExpand<subjects.length"><van-icon name="arrow-down" size="20"/>查看各科成绩</div>
          <div v-else><van-icon name="arrow-up" size="20"/>隐藏各科成绩</div>  
        </div>     
      </div>
      <Radar class="radar" :subjects="subjects" />
      <div style="height: 3rem;"/>
  </div>
</template>

<script>
import { ImagePreview } from 'vant';
import Radar from '../components/Radar.vue';
export default{
  props:['subjects'],
  components: {Radar},
  data () {
    return {
      maxExpand:0,
    };
  },
  methods:{
    viewWrongQuestion(item){
      return item.Score==item.totalScore?'':'color:red;';
    },
    imagePreview(images,index){
      ImagePreview({images:images,showIndex:true,loop:false,maxZoom:5,startPosition:index});
    },
    onExpand(){
      if(this.maxExpand>=this.subjects.length)
        this.maxExpand = 0;
      else
        this.maxExpand =this.subjects.length;
    },
  },
};
</script>

<style lang="less" scoped>
.table{
  border-radius: 10px;
  background-color: rgb(253, 251, 220);//rgb(221, 221, 221);
  margin: 20px 10px;
  color:black;
  line-height: 14px;
  padding: 10px;
}
.stat{
  display: grid;
  justify-items:center;
  align-items:center;
  width: 100%;
  height: 40px;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: auto auto;
}
.expand{
  height: 20px;
  text-align: center;
}
.radar{
  margin: 0px 10px; 
  width: 100%;
}
</style>



