<template>
  <div id="radar">
    <div id="canvas"  ref="workspace"
        :style="'width:'+(view.w-20)+'px;height:'+(view.w-20)+'px;'">
        <svg id="svgEditor"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            shape-rendering="auto"
            style=""
            width="100%" height="100%"
            viewBox="0 0 2000 2000">
          <g>
            <polygon v-for="(item,index) in points" :key="index"
              :points="item"
              stroke="grey"
              fill="none"        
              stroke-width="2"
              stroke-linecap="round"
              stroke-dasharray="10 10" />
          </g>
          <g v-for="(item,index) in info" :key="index+points.length">
            <line 
              x1="1000"
              y1="1000" 
              :x2="item.topPoint.x"
              :y2="item.topPoint.y"
              stroke="grey"       
              stroke-width="2"
              stroke-dasharray="10 10" />  

            <text 
              :x="item.textPoint.x"
              :y="item.textPoint.y"
              style="font-size:80px;fill:green;">{{item.name}}</text>
            <text 
              :x="item.gtPoint.x"
              :y="item.gtPoint.y"
              style="font-size:80px;fill:green;">{{item.level}}</text>              
          </g>
          <g>
            <polygon :points="gradePoints"
              stroke="blue"
              fill="red"        
              stroke-width="10"
              stroke-linecap="round"
              fill-opacity="0.1"/>            
          </g>
        </svg>
    </div>
  </div>
</template>

<script>
import ElementResizeDetectorMaker from 'element-resize-detector'
export default {
  props: ['subjects'],

  data () {
    return {
      view: {w: 0, h: 0, zoom_w: 1, zoom_h: 1},
      points:[],
      info:[],
      gradePoints:"",
    }
  },
  mounted(){
    this.initData();
    let that = this;
    window.onresize=()=>{
        return(()=>{
          that.view.w = document.body.clientWidth;
          that.view.h = document.body.clientHeight;          
        })();
    }

  },
  methods:{
    rotate(x,y,angle){
      let radian = Math.PI * angle / 180.0;
      let cosa = Math.cos(radian);
      let sina = Math.sin(radian);
      let x1 = cosa * x - sina * y;
      let y1 = cosa * y + sina * x;
      return {x:x1,y:y1};
    },
    getLevelGrade(level){
      if(level == undefined)
        return 0;
      const letter = { A: 5, B: 4, C: 3, D: 2, E: 1, }
      let grade = letter[level[0]] * 5 + 1 - level[1] * 1;
      return grade;      
    },
    initData(){
      this.info = [];
      this.subjects.forEach(el => {
        if(el.code >= 0){
          this.info.push({name:el.srcKM,
                         level:el.level,
                         grade:this.getLevelGrade(el.level),
                         topPoint:{x:0,y:0},
                         gradePoint:{x:0,y:0},
                         textPoint:{x:0,y:0},
                         gtPoint:{x:0,y:0}});
        }
          
      });

      let a = "",b="",c="",d="",e="";
      let x=0,y=0;
      this.points = [];
      this.gradePoints = "";
      if(this.info.length > 2){
        let angle = 360.0/this.info.length;
        let start = this.rotate(0.0,-800.0,-angle/2.0);
        this.info.forEach(el=>{
          el.topPoint.x = start.x+1000.0;
          el.topPoint.y = start.y+1000.0;
          a += el.topPoint.x +","+el.topPoint.y+" ";  
          b += (start.x*0.8+1000.0)+","+(start.y*0.8+1000.0)+" ";
          c += (start.x*0.6+1000.0)+","+(start.y*0.6+1000.0)+" ";  
          d += (start.x*0.4+1000.0)+","+(start.y*0.4+1000.0)+" "; 
          e += (start.x*0.2+1000.0)+","+(start.y*0.2+1000.0)+" "; 
          el.gradePoint.x = start.x*el.grade/25.0+1000.0;
          el.gradePoint.y = start.y*el.grade/25.0+1000.0; 

          if(el.gradePoint.x < 1000)
              el.gtPoint.x = el.gradePoint.x - 110;
          else
              el.gtPoint.x = el.gradePoint.x + 10;

          if(el.gradePoint.y < 1000)
              el.gtPoint.y = el.gradePoint.y;
          else
              el.gtPoint.y = el.gradePoint.y + 60;

          if(start.x<0)
            el.textPoint.x = start.x * 1.1+1000-80;
          else
            el.textPoint.x = start.x * 1.1+1000-40; 

          if(start.y<0)
            el.textPoint.y = start.y * 1.1+1000+20;
          else
            el.textPoint.y = start.y * 1.1+1000+40; 
            
          this.gradePoints += el.gradePoint.x+","+el.gradePoint.y+" "; 
          start = this.rotate(start.x,start.y,angle);

        });
      }
      this.points.push(a);
      this.points.push(b);  
      this.points.push(c);  
      this.points.push(d);        
      this.points.push(e); 
    }
  },
  watch:{
    subjects(oldVal,newVal){
        this.initData();
    }
  },
}
</script>

<style lang="less">
  #canvas{
    border-radius: 10px;
    background-color: rgb(253, 251, 220);
    margin: 0px 10px;
    padding:20px 10px;
  }
  #radar{
    display: inline;
    width:100%;
  }
</style>



