<template>
  <div>
    <div class="score-level">
      <div class="card">
      <div class="score">
        <span style="font-size:48px;color:red;">{{subData.myScore}}</span>
        <span>{{"/"+subData.fullScore}}</span> 
        <p> {{"(参与排名人数："+examJoin+"人)"}} </p>  
        <!--p> {{"(学校参与排名人数："+subData.total+"人)"}} </p-->    
      </div>
      <van-row  type="flex" justify="space-around">
        <van-col v-if="isUnion==true" style="text-align:center;" span="6"><p class="card-item">{{subData.us}}</p><p>联考排名</p></van-col>
        <van-col style="text-align:center;" span="6"><p class="card-item">{{subData.ss}}</p><p>学校排名</p></van-col>
        <van-col style="text-align:center;" span="6"><p class="card-item">{{subData.cs}}</p><p>班级排名</p></van-col>
        <van-col style="text-align:center;" span="6"><p class="card-item">{{subData.level}}</p><p>等级排名</p></van-col>
      </van-row>
      </div>

      <SubInfo v-if="subData.code>=0" :subData=subData />
      <StatInfo v-else :subjects=subjects />
    </div>
  </div>
</template>

<script>
import { ImagePreview } from 'vant';
import SubInfo from './SubInfo.vue';
import StatInfo from './StatInfo.vue';
export default{
  props:['subjects','subData','examJoin','isUnion'],
  components: {SubInfo,StatInfo},
};
</script>

<style lang="less" scoped>
.score-level {
  position: absolute;
  vertical-align:middle;
  margin: 0;
  height: 100px;
  width: 100%;
  line-height: 5px;
  font-size:14px;
  color:rgb(17, 122, 105);
}
.score{
  padding:40px 0 0 0;
  height:70px;
  text-align: center;

}
.card{
  background-color:rgb(253, 251, 220);
  border-radius: 10px;
  margin:10px 10px;
}
.card-item{
  font-size:20px;
  color:blue;
}
</style>



